.signIn{
    display:flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    height:100vh;
}
.signin__logo{
    margin-top:20px;
    margin-bottom: 20px;
    object-fit: contain;
    width:100px;
    margin-right: auto;
    margin-left: auto;
}
.signin__container{
     width:300px;
     height: fit-content;
     display:flex;
     flex-direction: column;
     border-radius: 5px;
     border: 1px solid lightgray;
     padding:20px;
}
.signin__container > h1{
    font-weight: 400;
    margin-bottom:20px;
    font-size: 28px;
}
.signin__container > form >h5{
    font-weight: 700;
    font-size: 13px;
    margin-bottom: 5px;
}
.signin__container > form > input{
    height:30px;
    margin-bottom:10px;
    width:98%;
    background-color: white;
}
.signin__container > p {
    margin-top:15px;
    font-size:12px;
}
.login__signInButton{
    background-color: #f0c14b;
    border-radius: 5px;
    width:100%;
    height: 30px;
    border:1px solid;
    margin-top:10px;
    border-color: #a88734 #9c7e31 #846a29;
}
.login__registerButton{
    border-radius:2px;
    width:100%;
    height:30px;
    border:1px solid;
    margin-top: 10px;
    border-color: darkgray;
}