*{
    box-sizing: border-box;
}
.home{
    display:flex;
    flex-direction: column;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
   max-width:1500px;
}

.home__image{
    width:100%;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    margin-bottom: -150px;
    z-index: -1;

}
.home__row{
   display:flex;
   z-index: 1;
   margin-left: 5px;
   margin-right: 5px;
}
